@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
header {
    /*height: 35vh;*/
    padding-top: 7rem;
    overflow: hidden;
}

.header__container{
    text-align:  center;
    height: 100%;
    position: relative;
}

/* ================= CTA ====================== */
.cta {
    margin-top: 2.5rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}

/* ================= SOCIALS ====================== */

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 10rem;
}

/* ================= SCROLL DOWN ====================== */
.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 10rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* =========== MEDIA QUERIES(TABLETS) ==========*/
@media screen and (max-width: 1024px) {
    header {
        /*height: 68vh;*/
    }
}

/* =========== MEDIA QUERIES(Phones) ==========*/
@media screen and (max-width: 600px) {
    header {
        /*height: 100vh;*/
    }

    .header__socials,
    .scroll__down{
        display:none;
    }
}
nav{
    background: rgba(0, 0, 0, 0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);

}

nav a {
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}

nav a:hover {
    background: rgba(0, 0, 0, 0.3);
}

nav a.active{
    background: var(--color-bg);
    color: var(--color-white);
}
.about__container{
    text-align: center;
    margin: auto;
    margin-top: -25px;
    inline-size: 350px;
    overflow-wrap: break-word;

}


.portfolio__container{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem;
}

.portfolio__item{
    background: var(--color-bg-variants);
    padding: 1.2rem;
    border-radius:  2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.portfolio__item:hover{
    border-color: var(--color-primary-variant);
    background: transparent;
}

.portfolio__item-image{
    border-radius: 1.5rem;
    overflow: hidden;
}

.portfolio__item h3 {
    margin: 1.2rem 0 2rem;
}

.portfolio__item-cta{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;


}

.portfolio__description {
    margin-top: 2rem;
}

/* ============= MEDIA QUERIES (MEDIUM) ================*/
@media screen and (max-width: 1024px){
.portfolio__container{
    grid-template-columns: 1fr 1fr;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}
}

/* ============= MEDIA QUERIES (small) ================*/
@media screen and (max-width: 600px){
    .portfolio__container{
        grid-template-columns: 1fr;
        grid-gap:1rem;
        gap:1rem;
    }
}

.container.contact__container{
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    grid-gap: 12%;
    gap: 12%;

}

.contact__options{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

.contact__option {
    background: var(--color-bg-variants);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border:  1px solid transparent;
    transition: var(--transition);
}

.contact__option:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
}

.contact__option-icon{
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.contact__option a{
    margin-top:  0.7rem;
    display: inline-block;
    font-size: 0.8rem;
}

/* ============= FORM ================*/
form {
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

input, textarea{
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid var(--color-primary-variant);
    resize: none;
    color: var(--color-white)
}

/* ============= MEDIA QUERIES (MEDIUM) ================*/
@media screen and (max-width: 1024px){
    .container.contact__container{
        grid-template-columns: 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }
}
    
/* ============= MEDIA QUERIES (small) ================*/
@media screen and (max-width: 600px){
    .container.contact__container{
        width: var(--container-width-sm);
    }
}
* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: #1f1f38;
    --color-bg-variants: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-variant: rgb(77, 181, 255, 0.4);
    --color-white: #fff;
    --color-light: rgba(255,255,255,0.6);

    --transition: all 400ms ease;
    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
}

html{
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}

body{
    font-family: 'Poppins', sans-serif;
    background: #1f1f38;
    background: var(--color-bg);
    color: #fff;
    color: var(--color-white);
    line-height: 1.7;
    /*background-image: url(.../src/assets/bg);*/
}

/* ========== GENERAL STYLES ==============*/
.container{
    width: 75%;
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1,h2,h3,h4,h5{
    font-weight: 500;
}

h1{
    font-size: 2.5rem;
}

section{
    margin-top: 8rem;
}

section > h2, section > h5{
  text-align: center;
  color: rgba(255,255,255,0.6);
  color: var(--color-light);  
}

section > h2{
    color: #4db5ff;
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light{
    color: rgba(255,255,255,0.6);
    color: var(--color-light);
}

a{
    color: #4db5ff;
    color: var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}
a:hover{
    color: #fff;
    color: var(--color-white);
}

.btn{
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #4db5ff;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #4db5ff;
    border: 1px solid var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

.btn:hover{
    background: #fff;
    background: var(--color-white);
    color: #1f1f38;
    color: var(--color-bg);
    border-color: transparent;

}

.btn-primary{
    background: #4db5ff;
    background: var(--color-primary);
    color: #1f1f38;
    color: var(--color-bg)
}

img{
    display: block;
    width: 100%;
    object-fit: cover;
}

/* =========== MEDIA QUERIES(TABLETS) ==========*/
@media screen and (max-width: 1024px) {
    .container{
        width: 86%;
        width: var(--container-width-md);
    }

    section{
        margin-top: 6rem;
    }
}

/* =========== MEDIA QUERIES(Phones) ==========*/
@media screen and (max-width: 600px) {
    .container{
        width: 90%;
        width: var(--container-width-sm);
    }
    
    section > h2{
        margin-top: 2rem;
    }
}


